<template>
  <div>
    <form @submit.prevent="updateEmpleado" class="employee-form">
      <fieldset class="form-section">
        <legend>Personal Information</legend>
        <div class="form-grid">
          <div class="form-group">
            <input type="hidden" id="id" v-model="localEmpleadoData.id" />
            <label for="nombre">First Name:<span class="required">*</span></label>
            <input type="text" id="nombre" v-model="localEmpleadoData.nombre" required />
            <span class="tooltipEmpleados">Enter your first name as it appears on your official documents. This may vary
              by
              nationality (e.g., given name in Western cultures).</span>
          </div>
          <div class="form-group">
            <label for="paterno">Last Name:<span class="required">*</span></label>
            <input type="text" id="paterno" v-model="localEmpleadoData.paterno" required />
            <span class="tooltipEmpleados">Enter your family name. In some cultures, this is the surname that identifies
              your
              family lineage.</span>
          </div>
          <div class="form-group">
            <label for="materno">Middle Name:</label>
            <input type="text" id="materno" v-model="localEmpleadoData.materno" />
            <span class="tooltipEmpleados">Enter any middle names or additional given names you may have, which may be
              common in
              certain cultures.</span>
          </div>
          <div class="form-group">
            <label for="telefono">Phone:<span class="required">*</span></label>
            <input type="text" id="telefono" v-model="localEmpleadoData.telefono" required />
            <span class="tooltipEmpleados">Enter your primary contact number. Ensure you include the country code if
              applicable.</span>
          </div>
          <div class="form-group">
            <label for="correo">Email:</label>
            <input type="email" id="correo" v-model="localEmpleadoData.correo"  />
            <span class="tooltipEmpleados">Enter a valid email address where you can be reached. This is important for
              communication.</span>
          </div>
          <div class="form-group">
            <label for="puesto">Position:</label>
            <input type="text" id="puesto" v-model="localEmpleadoData.puesto" />
            <span class="tooltipEmpleados">Enter your job title or position within the organization.</span>
          </div>
          <div class="form-group">
            <label for="edad">Date of Birth:</label>
            <input type="date" id="edad" v-model="localEmpleadoData.fecha_nacimiento" />
            <span class="tooltipEmpleados">Select your date of birth in the format of day, month, year. This is
              essential for
              identification.</span>
          </div>
          <div class="form-group">
            <label for="curp">CURP:</label>
            <input type="text" id="curp" v-model="localEmpleadoData.curp" />
            <span class="tooltipEmpleados">Enter your CURP (Unique Population Registry Code) if you are from Mexico.
              This
              identifies you uniquely in the system.</span>
          </div>
          <div class="form-group">
            <label for="nss">NSS:</label>
            <input type="text" id="nss" v-model="localEmpleadoData.nss" maxlength="20"/>
            <span class="tooltipEmpleados">Enter your NSS (Social Security Number) if applicable. This is crucial for
              social
              security services.</span>
          </div>
          <div class="form-group">
            <label for="rfc">RFC:</label>
            <input type="text" id="rfc" v-model="localEmpleadoData.rfc" maxlength="20" />
            <span class="tooltipEmpleados">Enter your RFC (Federal Taxpayer Registry) if you are from Mexico. This is
              important
              for tax identification.</span>
          </div>
          <div class="form-group">
            <label for="id">Employee ID:</label>
            <input type="text" id="id" v-model="localEmpleadoData.id_empleado" />
            <span class="tooltipEmpleados">Enter your unique employee identification number provided by your
              organization.</span>
          </div>
          <div class="form-group">
            <label for="creacion">Date of Entry:{{ localEmpleadoData.edicion }}</label>
            <input type="date" id="creacion" v-model="localEmpleadoData.creacion" />
            <span class="tooltipEmpleados">Select the employee's date of hire.</span>
          </div>
        </div>
      </fieldset>

      <fieldset class="form-section">
        <legend>Address</legend>
        <div class="form-grid">
          <div class="form-group">
            <label for="calle">Street:</label>
            <input type="hidden" id="id_domicilio" v-model="localEmpleadoData.domicilio_empleado.id" />
            <input type="text" id="calle" v-model="localEmpleadoData.domicilio_empleado.calle" placeholder="Street" />
            <span class="tooltipEmpleados">Enter the name of your street. This is the primary address line for your
              residence.</span>
          </div>
          <div class="form-group">
            <label for="num_ext">Exterior Number:</label>
            <input type="text" id="num_ext" v-model="localEmpleadoData.domicilio_empleado.num_ext"
              placeholder="Exterior Number" />
            <span class="tooltipEmpleados">Enter the exterior number of your residence, if applicable. This may be a
              house number
              or apartment number.</span>
          </div>
          <div class="form-group">
            <label for="num_int">Interior Number:</label>
            <input type="text" id="num_int" v-model="localEmpleadoData.domicilio_empleado.num_int"
              placeholder="Interior Number" />
            <span class="tooltipEmpleados">Enter the interior number if you live in an apartment or complex.</span>
          </div>
          <div class="form-group">
            <label for="colonia">Neighborhood:</label>
            <input type="text" id="colonia" v-model="localEmpleadoData.domicilio_empleado.colonia"
              placeholder="Neighborhood" />
            <span class="tooltipEmpleados">Enter the name of your neighborhood or locality, which may be important for
              delivery
              services.</span>
          </div>
          <div class="form-group">
            <label for="ciudad">City:</label>
            <input type="text" id="ciudad" v-model="localEmpleadoData.domicilio_empleado.ciudad" placeholder="City" />
            <span class="tooltipEmpleados">Enter the name of your city. This is essential for determining your
              location.</span>
          </div>
          <div class="form-group">
            <label for="estado">State:</label>
            <input type="text" id="estado" v-model="localEmpleadoData.domicilio_empleado.estado" placeholder="State" />
            <span class="tooltipEmpleados">Enter your state or province. This can vary depending on your country.</span>
          </div>
          <div class="form-group">
            <label for="pais">Country:</label>
            <input type="text" id="pais" v-model="localEmpleadoData.domicilio_empleado.pais" placeholder="Country" />
            <span class="tooltipEmpleados">Enter your country of residence. This is important for international
              identification.</span>
          </div>
          <div class="form-group">
            <label for="cp">Postal Code:</label>
            <input type="text" id="cp" v-model="localEmpleadoData.domicilio_empleado.cp" placeholder="Postal Code" />
            <span class="tooltipEmpleados">Enter the postal code for your address. This helps in accurately locating
              your
              residence.</span>
          </div>
        </div>
      </fieldset>

      <div class="button-container">
        <button type="submit" class="update-button">Update</button>
        <button class="close-button " type="button" @click="handleCloseModal">Close</button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone'; // Importa moment y moment-timezone

export default {
  name: 'GeneralInfoForm',
  props: {
    empleadoData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      localEmpleadoData: { ...this.empleadoData } // Crea una copia local
    };
  },
  methods: {
    async updateEmpleado() {
      const filteredData = Object.fromEntries(
        Object.entries(this.localEmpleadoData).filter(([key, value]) =>
          key !== 'status' && value !== '' && value !== null
        )
      );

      // Establecer la fecha de edición con la fecha y hora actual en la zona horaria de México
      filteredData.edicion = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      try {
        const apiUrl = `${this.$apiUrl}/empleados/update`;


        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.put(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response); // Ver respuesta completa

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualización exitosa',
          text: 'Los datos del empleado se han actualizado correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          // Emitir evento solo después de que el usuario acepte el mensaje
          this.$emit('empleadoActualizado', filteredData);
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al actualizar el empleado.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar el empleado:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    },

    handleCloseModal() {
      this.$emit('close'); // Emite el evento para cerrar el modal
    },
  },
  watch: {
    empleadoData: {
      handler(newValue) {
        this.localEmpleadoData = { ...newValue }; // Actualiza la copia local si la prop cambia
      },
      
      deep: true
    }
  }
};
</script>


<style scoped>
.employee-form {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.form-section {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
}

.form-section legend {
  font-weight: bold;
  margin-bottom: 10px;
}

.form-grid {
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(1, 1fr);
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.update-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 10px;
  /* Espacio entre botones */
}

.update-button:hover {
  background-color: #0056b3;
}

.close-button {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
}

.close-button:hover {
  background-color: #c82333;
}

.button-container {
  display: flex;
  justify-content: flex-end;
  /* Alinea los botones a la derecha */
  gap: 10px;
}

/* Media Queries */
@media (min-width: 600px) {
  .form-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 900px) {
  .form-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.tooltipEmpleados {
  display: none;
  position: absolute;
  background-color: #4c6fa6a7;
  color: #fff;
  padding: 5px;
  border-radius: 10px;
  z-index: 10;
  bottom: 100%;
  /* Position above the label */
  left: 0;
  /* Align with the left edge */
  margin-bottom: 5px;
  /* Add some space between the tooltipEmpleados and the label */
}

.form-group {
  position: relative;
  /* Required for absolute positioning of tooltipEmpleados */
}

.form-group:hover .tooltipEmpleados {
  display: block;
}
</style>