<template>
  <div>
    <p>This section manages essential employee file documents...</p>

    <div class="header">
      <input type="text" class="search" placeholder="Search" v-model="searchQuery" />
      <button @click="openAddDocumentModal">Add Document</button>
    </div>
    <div class="document-table-wrapper">
      <table class="document-table">
        <thead>
          <tr>
            <th>Name</th>
            <th>Upload Date</th>
            <th>Expired Date</th>
            <th>Days Remaining</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(doc, index) in filteredDocuments" :key="index">
            <td style="position: relative;">
              {{ doc.optionName }} {{ doc.nameAlterno }}
              <span class="tooltipDocuments">{{ doc.description }}</span>
            </td>
            <td>{{ doc.upload_date }}</td>
            <td>
              {{ doc.expiry_date }}
              <div style="position: relative; display: inline-block;">
                <i class="fas fa-sync" @click="openUpdateModal(doc)"
                  style="cursor: pointer; margin-left: 30px; color: #59ae38; font-size: 1.5em;"></i>
                <span class="tooltipDocuments">Update</span>
              </div>
            </td>
            <td>
              <div :style="getStyle(calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder), doc.expiry_reminder)"
                class="tooltipDocuments-container" style="position: relative;">

                Days <strong>{{ calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder) }}</strong>
                <span class="tooltipDocuments">Remind me {{ doc.expiry_reminder }} days before</span>
              </div>
            </td>
            <td>
              <a :href="doc.url" target="_blank">
                <button class="download-button" @click="openDocument(doc)">Open</button>
              </a>
              <button @click="deleteDocument(doc)" class="delete-button">Delete</button>
            </td>
          </tr>
        </tbody>


      </table>
    </div>

    <AddDocumentModal v-if="isModalVisible" :isVisible="isModalVisible" :employeeId="empleadoData.id"
      :id_portal="empleadoData.id_portal" @close="closeAddDocumentModal" @save="saveDocument"
      @documentoSubido="fetchDocuments" />

    <div class="modal" v-if="isUpdateModalVisible">
      <div class="modal-content">
        <span class="close" @click="closeUpdateModal">&times;</span>
        <h2>Update Expiry Date</h2>
        <form @submit.prevent="submitUpdate">
          <label for="expiry-date">Expiry Date:</label>
          <input id="expiry-date" v-model="newExpiryDate" type="date" required />

          <label for="expiryReminder">Expiration Reminder:</label>
          <select v-model="selectedReminder" @change="updateReminder">
            <option value="">Do not notify</option>
            <option value="1">1 day before</option>
            <option value="7">1 week before</option>
            <option value="15">15 days before</option>
            <option value="30">1 month before</option>
            <option value="custom">Custom...</option>
          </select>
          <div v-if="isCustomReminder">
            <label for="customReminder">Days before:</label>
            <input type="number" id="customReminder" v-model="customReminder" min="0" placeholder="Enter days"
              @input="updateReminder" />
          </div>

          <div class="modal-buttons">
            <button type="submit">Save</button>
            <button type="button" @click="closeUpdateModal">Cancel</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import AddDocumentModal from './AddDocumentModal.vue';
import Swal from 'sweetalert2';

export default {
  components: {
    AddDocumentModal,
  },
  props: {
    empleadoData: Object,
  },
  data() {
    return {
      isModalVisible: false,
      isUpdateModalVisible: false,
      newExpiryDate: '',
      documents: [],
      selectedDocument: null,
      selectedReminder: '0',
      customReminder: '',
      isCustomReminder: false,
      searchQuery: '',
    };
  },
  computed: {
    filteredDocuments() {
      if (!this.documents) return []; // Asegura que documents esté definido

      const query = this.searchQuery.toLowerCase();
      return this.documents.filter(doc => {
        const optionName = doc.optionName ? doc.optionName.toLowerCase() : '';
        const uploadDate = doc.upload_date ? doc.upload_date.toLowerCase() : '';
        const expiryDate = doc.expiry_date ? doc.expiry_date.toString() : '';

        return optionName.includes(query) ||
          uploadDate.includes(query) ||
          expiryDate.includes(query);
      });
    }
  },
  mounted() {
    this.fetchDocuments();
  },
  watch: {
    documents: {
      handler(newDocs) {
        newDocs.forEach(doc => {
          doc.daysRemaining = this.calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder);
        });
      },
      deep: true,
    },
    newExpiryDate() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.newExpiryDate, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    customReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    selectedReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
  },
  methods: {
    
    openDocument(doc) {
      // Imprime la URL base

      const fileName = doc.nameDocument || 'default_filename.png'; // Asegúrate de que nameDocument esté definido
      const url = `${this.$documentUrl}_documentEmpleado/${fileName}`; // Usa el nombre del archivo
      window.open(url, '_blank'); // Abre el documento en una nueva pestaña
    },
    
   



    openUpdateModal(doc) {
      this.selectedDocument = doc;
      this.newExpiryDate = doc.expiry_date;
      this.selectedReminder = doc.expiry_reminder || '0';
      this.isCustomReminder = this.selectedReminder === 'custom';
      this.customReminder = this.selectedReminder !== 'custom' ? this.selectedReminder : '';
      this.isUpdateModalVisible = true;
    },

    closeUpdateModal() {
      this.isUpdateModalVisible = false;
      this.selectedDocument = null;
    },

    async submitUpdate() {
      if (this.newExpiryDate) {
        try {
          const response = await fetch(`${this.$apiUrl}/documents/${this.selectedDocument.id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              table: 'documents_empleado',
              expiry_date: this.newExpiryDate,
              expiry_reminder: this.isCustomReminder ? this.customReminder : this.selectedReminder,
            }),
          });

          if (!response.ok) {
            throw new Error('Error updating expiry date');
          }

          // Actualizar la fecha localmente (opcional si solo se quiere actualizar desde el servidor)
          this.selectedDocument.expiry_date = this.newExpiryDate;
          this.selectedDocument.expiry_reminder = this.isCustomReminder ? this.customReminder : this.selectedReminder;

          // Recargar los documentos para que la tabla se actualice
          await this.fetchDocuments();

          // Alerta de éxito
          await Swal.fire({
            icon: 'success',
            title: 'Success!',
            text: 'Expiry date updated successfully.',
            confirmButtonText: 'OK'
          });

          this.closeUpdateModal();
        } catch (error) {
          console.error(error);
          await Swal.fire({
            icon: 'error',
            title: 'Error!',
            text: 'Could not update the expiry date.',
            confirmButtonText: 'OK'
          });
        }
      }
    },
    calculateDaysRemaining(expiryDate, daysBefore) {

      const today = new Date();
      // Ajusta la fecha de hoy a medianoche
      today.setHours(0, 0, 0, 0);

      const expiry = new Date(expiryDate);
      // Ajusta la fecha de expiración a medianoche
      expiry.setHours(0, 0, 0, 0);

      // Verifica si daysBefore es null
      if (daysBefore === null) {
        return 'Do not notify'; // Devuelve un valor en blanco
      }

      // Calcula la fecha de advertencia
      const warningDate = new Date(expiry);
      warningDate.setDate(warningDate.getDate() - daysBefore); // Usa daysBefore aquí

      // Calcula los días restantes
      const timeDiff = expiry - today;
      const retornar = Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1;
      // console.log("🚀 ~ calculateDaysRemaining ~ retornar:", retornar)
      return retornar; // Días restantes
    },


    getStyle(timeDiff, daysBefore) {

      if (daysBefore === null) {
        return {
          backgroundColor: '#ccffcc',
          color: 'black',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la fecha de expiración ha pasado o es hoy
      if (timeDiff <= 0) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la diferencia en días es igual a daysBefore
      if (timeDiff <= daysBefore) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue (advertencia)
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si está dentro de un rango de 5 días antes de daysBefore
      if (timeDiff > daysBefore && timeDiff <= (daysBefore + 5)) {
        return {
          backgroundColor: 'rgba(205, 202, 0, 0.915)', // amarillo tenue
          padding: '10px',
          color: 'white',
          borderRadius: '5px',
        };
      }

      // Si no cumple ninguna de las condiciones anteriores, es verde
      return {
        backgroundColor: 'rgba(12, 232, 34, 0.922)', // verde tenue
        color: 'white',
        padding: '10px',
        borderRadius: '5px',
      };
    },

    async fetchDocuments() {
      try {
        console.log('Fetching documents for employee ID:', this.empleadoData.id); // Log del ID del empleado
        const response = await fetch(`${this.$apiUrl}/documents/${this.empleadoData.id}`);

        if (!response.ok) {
          throw new Error('No se pudo obtener los documentos');
        }

        const data = await response.json();
        console.log('Fetched documents:', data); // Log de los documentos obtenidos

        this.documents = data.documentos.map(doc => ({
          ...doc,
          daysRemaining: this.calculateDaysRemaining(doc.expiry_date, doc.expiry_reminder),
        }));

        //this.filteredDocuments = this.documents; // Inicializa filteredDocuments con los documentos obtenidos

      } catch (error) {
        console.error('Error fetching documents:', error); // Log del error
        this.documents = this.generateSampleDocuments(); // Asigna documentos de ejemplo en caso de error
        this.filteredDocuments = this.documents; // Inicializa filteredDocuments con documentos de ejemplo
      }
    },

    closeAddDocumentModal() {
      this.isModalVisible = false;
    },
    generateSampleDocuments() {
      return [
        {
          optionName: 'Ejemplo 1',
          upload_date: new Date().toISOString().split('T')[0],
          expiry_date: new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString().split('T')[0],
          url: '#',
        },
        {
          optionName: 'Ejemplo 2',
          upload_date: new Date().toISOString().split('T')[0],
          expiry_date: new Date(new Date().setFullYear(new Date().getFullYear() + 1)).toISOString().split('T')[0],
          url: '#',
        },
      ];
    },
    openAddDocumentModal() {
      this.isModalVisible = true;
    },
    saveDocument(formData) {
      this.$emit('add-document', formData);
      this.closeAddDocumentModal();
    },
    deleteDocument(doc) {
      Swal.fire({
        title: 'Are you sure?',
        text: "This document will be permanently deleted.",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, Delete it!'
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            // Log the document name

            const response = await fetch(`${this.$apiUrl}/documents`, {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                id: doc.id,
                nameDocument: doc.nameDocument,
                tabla: 'documentos',
              }),
            });

            if (!response.ok) {
              throw new Error('Error deleting the document');
            }

            // Update the document list
            this.documents = this.documents.filter(d => d.id !== doc.id);
            Swal.fire('Deleted!', 'The document has been deleted.', 'success');
          } catch (error) {
            Swal.fire('Error!', error.message, 'error');
          }
        }
      });
    },

    updateReminder() {
      if (this.selectedReminder === 'custom') {
        this.isCustomReminder = true;
      } else {
        this.isCustomReminder = false;
        this.customReminder = this.selectedReminder;
      }
    },
  },
};
</script>




<style scoped>
.header {
  display: flex;
  align-items: center;
  /* Alineación vertical */
  gap: 10px;
  /* Espacio entre el input y el botón */
}

.header input[type="text"] {
  flex-grow: 1;
  /* El input ocupa el espacio restante */
  height: 40px;
  /* Altura fija */
  padding: 0 10px;
  /* Espaciado interno */
  border: 1px solid #ccc;
  /* Bordes */
  border-radius: 4px;
  /* Bordes redondeados */
  font-size: 16px;
  /* Tamaño de fuente */
  box-sizing: border-box;
  /* Incluye padding y borde en el tamaño total */
  line-height: 1;
  /* Asegura que la altura de línea no cause problemas */
  margin: 0;
  /* Asegura que no haya márgenes */
}

.header button {
  background-color: #007bff;
  /* Color de fondo azul */
  color: white;
  /* Color del texto blanco */
  border: none;
  /* Sin bordes */
  border-radius: 4px;
  /* Bordes redondeados */
  height: 40px;
  /* Igual a la altura del input */
  padding: 0 15px;
  /* Espaciado interno */
  cursor: pointer;
  /* Cambia el cursor al pasar el mouse */
  font-size: 16px;
  /* Tamaño de fuente */
  display: flex;
  /* Para alinear el texto */
  align-items: center;
  /* Alineación vertical */
  justify-content: center;
  /* Centra el texto */
  transition: background-color 0.3s;
  /* Transición suave */
  line-height: 1;
  /* Asegura que la altura de línea no cause problemas */
  margin: 0;
  /* Asegura que no haya márgenes */
}

.header button:hover {
  background-color: #0056b3;
  /* Color al pasar el mouse */
}

button:hover {
  background-color: #0056b3;
  /* Color al pasar el mouse */
}




.document-table-wrapper {
  max-height: 60vh;
  overflow-y: auto;
  margin-top: 20px;
}


.document-table {
  width: 100%;
  border-collapse: collapse;
}

.document-table th,
.document-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.document-table th {
  background-color: #f2f2f2;
}

.download-button,
.delete-button {
  padding: 5px 10px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.download-button {
  background-color: #007bff;
  color: white;
}

.delete-button {
  background-color: #dc3545;
  color: white;
  margin-left: 5px;
}

.modal {
  display: block;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.216);
}

.modal-content {
  background-color: #fefefed8;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #6f6464b3;
  width: 300px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-buttons button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
  font-size: 16px;
  transition: background-color 0.3s;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
}

input {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

select {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  text-transform: uppercase;

}

.modal-buttons button[type="button"] {
  background: #dc3545;
}

.modal-buttons button:hover {
  background: #0056b3;
}

.modal-buttons button[type="button"]:hover {
  background: #c82333;
}

.tooltipDocuments {
  visibility: hidden;
  width: 150px;
  background-color: #4c6fa6c5;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  /* Muestra arriba del ícono */
  left: 50%;
  margin-left: -75px;
  /* Centrar el tooltipDocuments */
  opacity: 0;
  transition: opacity 0.3s;
}

td:hover .tooltipDocuments {
  visibility: visible;
  /* Muestra el tooltipDocuments al pasar el mouse */
  opacity: 1;
  /* Hace el tooltipDocuments visible */
}

.document-table-wrapper div:hover .tooltipDocuments {
  visibility: visible;
  opacity: 1;
}
</style>
