<template>
  <div class="profile-tabs">
    <button @click="setTab('general')" :class="{ active: currentTab === 'general' }">General Information</button>
    <button @click="setTab('medical')" :class="{ active: currentTab === 'medical' }">Medical Information</button>
    <button @click="handleDocumentsClick" :class="{ active: currentTab === 'documents' }">Employee Files
      <span v-if="status === 'amarillo' || status === 'rojo'" class="alert-symbol" :class="status"></span>
    </button>
    <button @click="setTab('bgv')" :class="{ active: currentTab === 'bgv' }">BGV & Health Records</button>
  </div>
</template>

<script>
export default {
  props: {
    currentTab: String,
    status: String,
  },
  methods: {
    setTab(tab) {
      this.$emit('update:tab', tab);
    },
    handleDocumentsClick() {
      this.setTab('documents'); // Cambia la pestaña
      this.$nextTick(() => {
        // Elimina el span después de que se haya procesado el clic
        const span = event.currentTarget.querySelector('.alert-symbol');
        if (span) {
          span.style.display = 'none'; // Oculta el span
        }
      });
    }
  }
};
</script>

<style scoped>

/* Ajustes generales */
.profile-tabs {
  display: flex;
  flex-wrap: wrap; /* Permite que las pestañas se acomoden en múltiples filas */
  justify-content: center; /* Centra las pestañas en dispositivos pequeños */
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
  padding: 10px 5px; /* Espaciado para pantallas pequeñas */
}

.profile-tabs button {
  background: none;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  color: #007bff;
  transition: border-bottom 0.3s ease, font-size 0.3s ease;
  text-align: center;
  flex: 1 0 auto; /* Permite ajustar el ancho según el contenido */
}

.profile-tabs button.active {
  border-bottom: 3px solid #007bff;
  font-weight: bold;
}

/* Ajustes responsivos */
@media (max-width: 600px) {
  .profile-tabs {
    flex-direction: column; /* Cambia la dirección a columnas */
    align-items: stretch; /* Asegura que las pestañas ocupen todo el ancho */
    padding: 10px 0; /* Reduce el padding */
  }

  .profile-tabs button {
    font-size: 14px; /* Reduce el tamaño de fuente */
    padding: 8px 10px; /* Reduce el padding */
    text-align: left; /* Alinea el texto a la izquierda */
  }

  .profile-tabs button.active {
    border-bottom: none; /* Elimina la línea de fondo */
    border-left: 3px solid #007bff; /* Añade una línea a la izquierda para diferenciar */
  }
}

/* Alerta simbólica responsiva */
.alert-symbol {
  width: 8px; /* Ajusta el tamaño en pantallas pequeñas */
  height: 8px;
}

@media (min-width: 600px) {
  .alert-symbol {
    width: 10px;
    height: 10px;
  }
}

@media (min-width: 900px) {
  .alert-symbol {
    width: 12px;
    height: 12px;
  }
}

.alert-symbol {
  display: inline-block;
  width: 10px; /* Ajusta el tamaño */
  height: 10px; /* Ajusta el tamaño */
  border-radius: 50%;
  margin-left: 5px; /* Espaciado entre el texto y el círculo */
}

.amarillo {
  background-color: rgba(205, 202, 0, 0.915);
}

.rojo {
  background-color: rgba(255, 0, 0, 0.906);
}
</style>
