<template>
  <div class="modal" v-if="show" @click="closeModal">
    <div class="modal-content" @click.stop>
      <h5 class="employee-name">{{ empleados.nombre }} {{ empleados.paterno }} {{ empleados.materno }}</h5>
      <p class="info-text">Here you can upload internal courses and training programs.</p>

      <div class="header">
        <input type="text" class="search" placeholder="Search" v-model="searchQuery" />
        <button class="register-course-button" @click="openAddCursosModal">Register New</button>
      </div>

      <div class="courses-table-wrapper">
        <table class="courses-table">
          <thead>
            <tr>
              <th>ID</th>
              <th>Name</th>
              <th>Registration Date</th>
              <th>Expiration Date</th>
              <th>Days Remaining</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-if="filteredCourses.length === 0">
              <td colspan="6" class="no-courses">Courses for the employee have not been loaded yet.</td>
            </tr>
            <tr v-for="(course, index) in filteredCourses" :key="index">
              <td>{{ course.id }}</td>
              <td>{{ course.name }}</td>
              <td>{{ course.creacion }}</td>
              <td>{{ course.expiry_date }}
                <div style="position: relative; display: inline-block;">
                  <i class="fas fa-sync" @click="openUpdateModal(course)"
                    style="cursor: pointer; margin-left: 30px; color: #59ae38; font-size: 1.5em;"></i>
                  <span class="tooltipDocuments">Update</span>
                </div>
              </td>
              <td>
                <div
                  :style="getStyle(calculateDaysRemaining(course.expiry_date, course.expiry_reminder), course.expiry_reminder)"
                  class="tooltipDocuments-container" style="position: relative;">
                  Days <strong>{{ calculateDaysRemaining(course.expiry_date, course.expiry_reminder) }}</strong>
                  <span class="tooltipDocuments">Remind me {{ course.expiry_reminder }} days before</span>
                </div>
              </td>
              <td>
                <div class="modal-buttons">
                  <button @click="openEvidence(course)" class="icon-button" aria-label="View">
                    <i class="fas fa-eye"></i> <!-- Icono para Ver -->
                  </button>

                  <!-- Icono para Eliminar -->
                  <button type="button" @click="deleteCourse(course)" class="icon-button" aria-label="Delete">
                    <i class="fas fa-trash-alt"></i> <!-- Icono para Eliminar -->
                  </button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div class="modal-footer">
        <button class="close-button" @click="closeModal">Close</button>
      </div>
    </div>
    <!-- Modal for Update -->
    <div class="modal-U" v-if="isUpdateModalVisible">
      <div class="modal-content-U">
        <span class="close" @click.stop="closeUpdateModal">&times;</span>
        <h2>Update Expiry Date</h2>
        <form @submit.prevent="submitUpdate">
          <label for="expiry-date">Expiry Date:</label>
          <input id="expiry-date" v-model="newExpiryDate" type="date" required />
          <label for="expiryReminder">Expiration Reminder:</label>
          <select v-model="selectedReminder" @change="updateReminder" @click.stop>
            <option value="">Do not notify</option>
            <option value="1">1 day before</option>
            <option value="7">1 week before</option>
            <option value="15">15 days before</option>
            <option value="30">1 month before</option>
            <option value="custom">Custom...</option>
          </select>
          <div v-if="isCustomReminder">
            <label for="customReminder">Days before:</label>
            <input type="number" id="customReminder" v-model="customReminder" min="0" placeholder="Enter days"
              @input="updateReminder" />
          </div>

          <div class="modal-buttons">
            <button type="submit">Save</button>
            <button type="button" @click.stop="closeUpdateModal">Cancel</button>
          </div>
        </form>
      </div>
    </div>

    <AddCursosModal v-if="isAddModalVisible" :isVisible="isAddModalVisible" :employeeId="empleados.id"
      :idPortal="empleados.id_portal" @close="isAddModalVisible = false" @courseAdded="fetchCursos" :origen="origen" />
  </div>
</template>



<script>
import AddCursosModal from './AddCursosModal.vue';
import api from '@/services/apiEmpleados'; // Asegúrate de que la ruta sea correcta
import Swal from 'sweetalert2';

export default {
  props: {
    empleados: { type: Object, required: true },
    show: { type: Boolean, default: false },
    origen: { type: Number, required: true },
    idPortal: { type: String, default: '' },
  },
  components: {
    AddCursosModal
  },
  data() {
    return {
      courses: [],
      searchQuery: '',
      isAddModalVisible: false,
      isUpdateModalVisible: false,
      customReminder: '',
      isCustomReminder: false,
      selectedDocument: null,
      newExpiryDate: '',
      selectedReminder: '0',


    };
  },
  computed: {
    filteredCourses() {
      if (!this.courses) return [];
      const query = this.searchQuery.toLowerCase();
      return this.courses.filter(course => {
        return (
          course.name.toLowerCase().includes(query) ||
          course.creacion.includes(query) ||
          course.expiry_date.includes(query)
        );
      });
    }
  },
  watch: {
    courses: {
      handler(newDocs) {
        newDocs.forEach(course => {
          course.daysRemaining = this.calculateDaysRemaining(course.expiry_date, course.expiry_reminder);
        });
      },
      deep: true,
    },
    newExpiryDate() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.newExpiryDate, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    customReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
    selectedReminder() {
      if (this.selectedDocument) {
        this.selectedDocument.daysRemaining = this.calculateDaysRemaining(this.selectedDocument.expiry_date, this.isCustomReminder ? this.customReminder : this.selectedReminder);
      }
    },
  },
  methods: {
    openUpdateModal(course) {
      this.selectedDocument = course;
      this.newExpiryDate = course.expiry_date;
      this.selectedReminder = course.expiry_reminder || '0';
      this.isCustomReminder = this.selectedReminder === 'custom';
      this.customReminder = this.selectedReminder !== 'custom' ? this.selectedReminder : '';
      this.isUpdateModalVisible = true;
    },
    closeUpdateModal() {
      this.isUpdateModalVisible = false;
      this.selectedDocument = null;
    },

    openAddCursosModal() {
      this.isAddModalVisible = true;
    },

    closeAddCursosModal() {
      this.isAddModalVisible = false;
    },

    closeModal() {
      // Solo cierra si no hay otros modales abiertos
      if (!this.isUpdateModalVisible && !this.isAddModalVisible) {
        this.$emit('close');
        this.$emit('update');
      }
    },
    openEvidence(course) {


      const fileName = course.name_document || 'default_filename.png'; // Asegúrate de que nameDocument esté definido
      const url = `${this.$documentUrl}_cursos/${fileName}`; // Usa el nombre del archivo
      window.open(url, '_blank');
    },
    deleteCourse(course) {
      Swal.fire({
        title: 'Are you sure?',
        text: "This document will be permanently deleted.",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Yes, Delete it!',
        width: '90%', // Establece un ancho responsivo
        padding: '20px', // Añadir un poco de relleno para que se vea bien
        customClass: {
          popup: 'responsive-popup', // Agrega una clase personalizada para estilos adicionales
        }
      }).then(async (result) => {
        if (result.isConfirmed) {
          try {
            // Log the document name

            const response = await fetch(`${this.$apiUrl}/documents`, {
              method: 'DELETE',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({
                id: course.id,
                nameDocument: course.name_document,
                tabla: 'cursos',
              }),
            });

            if (!response.ok) {
              throw new Error('Error deleting the document');
            }

            // Update the document list
            this.courses = this.courses.filter(c => c.id !== course.id);
            Swal.fire('Deleted!', 'The document has been deleted.', 'success');
          } catch (error) {
            Swal.fire('Error!', error.message, 'error');
          }
        }
      });
    },

    fetchCursos() {
      const apiUrl = `${this.$apiUrl}/cursos/empleado`; // Ajusta la URL a la ruta correcta
      const employeeId = this.empleados.id; // Suponiendo que tienes el ID del empleado en `this.empleados.id`
      const origen = this.origen; // Suponiendo que tienes el origen en `this.empleados.origen`
      api.get(apiUrl, { params: { employee_id: employeeId, origen: origen } })
        .then(response => {
          if (response.data && Array.isArray(response.data)) {
            this.courses = response.data;

          } else {
            this.generateRandomCourses(); // Cambia esto si `generateRandomCourses` no retorna un array
          }
        })
        .catch(error => {
          console.error('Error fetching cursos:', error);
          // Generar cursos aleatorios en caso de error
        });
    },
    calculateDaysRemaining(expiryDate, daysBefore) {

      const today = new Date();
      // Ajusta la fecha de hoy a medianoche
      today.setHours(0, 0, 0, 0);

      const expiry = new Date(expiryDate);
      // Ajusta la fecha de expiración a medianoche
      expiry.setHours(0, 0, 0, 0);

      // Verifica si daysBefore es null
      if (daysBefore === null) {
        return 'Do not notify'; // Devuelve un valor en blanco
      }

      // Calcula la fecha de advertencia
      const warningDate = new Date(expiry);
      warningDate.setDate(warningDate.getDate() - daysBefore); // Usa daysBefore aquí

      // Calcula los días restantes
      const timeDiff = expiry - today;
      const retornar = Math.ceil(timeDiff / (1000 * 3600 * 24)) + 1;
      // console.log("🚀 ~ calculateDaysRemaining ~ retornar:", retornar)
      return retornar; // Días restantes
    },
    generateRandomCourses() {
      const courseNames = ['Course A', 'Course B', 'Course C', 'Training 1', 'Training 2'];
      this.courses = Array.from({ length: 10 }, (_, index) => {
        const creacion = this.getRandomDate();
        const expiry_date = Math.random() < 0.7 ? this.getRandomNearDate() : this.getRandomDate(30);
        const expiry_reminder = this.calculateexpiry_reminder(expiry_date);
        const style = this.getStyle(expiry_reminder, 5);

        return {
          id: index + 1,
          name: courseNames[Math.floor(Math.random() * courseNames.length)],
          creacion,
          expiry_date,
          expiry_reminder,
          style
        };
      });
    },


    getStyle(timeDiff, daysBefore) {

      if (daysBefore === null) {
        return {
          backgroundColor: '#ccffcc',
          color: 'black',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la fecha de expiración ha pasado o es hoy
      if (timeDiff <= 0) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si la diferencia en días es igual a daysBefore
      if (timeDiff <= daysBefore) {
        return {
          backgroundColor: 'rgba(255, 0, 0, 0.906)', // rojo tenue (advertencia)
          color: 'white',
          padding: '10px',
          borderRadius: '5px',
        };
      }

      // Verificar si está dentro de un rango de 5 días antes de daysBefore
      if (timeDiff > daysBefore && timeDiff <= (daysBefore + 5)) {
        return {
          backgroundColor: 'rgba(205, 202, 0, 0.915)', // amarillo tenue
          padding: '10px',
          color: 'white',
          borderRadius: '5px',
        };
      }

      // Si no cumple ninguna de las condiciones anteriores, es verde
      return {
        backgroundColor: 'rgba(12, 232, 34, 0.922)', // verde tenue
        color: 'white',
        padding: '10px',
        borderRadius: '5px',
      };
    },
    getRandomDate() {
      const start = new Date();
      const end = new Date(start);
      end.setFullYear(end.getFullYear() + 1); // Expira en un año
      const randomDate = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()));
      return randomDate.toISOString().split('T')[0]; // Formato YYYY-MM-DD
    },
    getRandomNearDate() {
      const today = new Date();
      const daysToAdd = Math.floor(Math.random() * 10); // Genera un número entre 0 y 9
      const nearexpiry_date = new Date(today);
      nearexpiry_date.setDate(today.getDate() + daysToAdd);
      return nearexpiry_date.toISOString().split('T')[0]; // Formato YYYY-MM-DD
    },

    updateReminder() {
      if (this.selectedReminder === 'custom') {
        this.isCustomReminder = true;
      } else {
        this.isCustomReminder = false;
        this.customReminder = this.selectedReminder;
      }
    },
    async submitUpdate() {
      if (this.newExpiryDate) {
        try {
          const response = await fetch(`${this.$apiUrl}/documents/${this.selectedDocument.id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              table: 'cursos_empleados',
              expiry_date: this.newExpiryDate,
              expiry_reminder: this.isCustomReminder ? this.customReminder : this.selectedReminder,
            }),
          });

          if (!response.ok) {
            throw new Error('Error updating expiry date');
          }

          // Actualizar la fecha localmente (opcional si solo se quiere actualizar desde el servidor)
          this.selectedDocument.expiry_date = this.newExpiryDate;
          this.selectedDocument.expiry_reminder = this.isCustomReminder ? this.customReminder : this.selectedReminder;

          // Recargar los documentos para que la tabla se actualice
          await this.fetchCursos();

          // Alerta de éxito
          await Swal.fire({
            icon: 'success',
            title: 'Success!',
            text: 'Expiry date updated successfully.',
            confirmButtonText: 'OK'
          });

          this.closeUpdateModal();
        } catch (error) {
          console.error(error);
          await Swal.fire({
            icon: 'error',
            title: 'Error!',
            text: 'Could not update the expiry date.',
            confirmButtonText: 'OK'
          });
        }
      }
    },

  },
  mounted() {
    this.fetchCursos(); // Llama a la función al montar el componente
  }
};
</script>




<style scoped>
.no-courses {
  text-align: center;
  color: #777;
  padding: 40px 0;
  font-size: 24px;
  font-weight: bold;
}

.modal {
  display: block;
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 95%;
  height: 90%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: rgba(255, 255, 255, 0.9);
  margin: 5% auto;
  padding: 20px;
  border: 1px solid #6f6464b3;
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  height: auto;
  width: 95%;
  max-height: 95%;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  margin-top: auto;
}

.header {
  display: flex;
  align-items: center;
  gap: 10px;
}

.header input[type="text"] {
  flex-grow: 1;
  height: 40px;
  padding: 0 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.header button {
  background-color: rgba(10, 142, 23, 0.922);
  color: white;
  border: none;
  border-radius: 4px;
  height: 40px;
  padding: 0 15px;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s;
}

.header button:hover {
  background-color: #0056b3;
}

.courses-table-wrapper {
  max-height: 80%;
  overflow-y: auto;
  margin-top: 20px;
}

.courses-table {
  width: 100%;
  max-height: 80%;
  border-collapse: collapse;
}

.courses-table th,
.courses-table td {
  padding: 10px;
  border: 1px solid #ddd;
  text-align: left;
}

.courses-table th {
  background-color: #f2f2f2;
}

.register-course-button,
.close-button {
  padding: 10px 15px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s;
}

.register-course-button {
  background-color: #007bff;
  color: white;
  margin-bottom: 20px;
}

.register-course-button:hover {
  background-color: #0056b3;
}

.close-button {
  background-color: #dc3545;
  color: white;
  margin-top: 20px;
}

.close-button:hover {
  background-color: #c82333;
}

select {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  text-transform: uppercase;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
}

.modal-buttons button {
  background: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  margin-left: 10px;
  font-size: 16px;
  transition: background-color 0.3s;
}

.modal-buttons button[type="button"] {
  background: #dc3545;
}

.modal-buttons button:hover {
  background: #0056b3;
}

.modal-buttons button[type="button"]:hover {
  background: #c82333;
}

.info-text {
  font-size: 14px;
  color: #555;
  margin: 10px 0;
}

h2 {
  justify-content: flex-start;
}

.tooltipDocuments {
  visibility: hidden;
  width: 150px;
  background-color: #4c6fa6c5;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -75px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltipDocuments-container:hover .tooltipDocuments {
  visibility: visible;
  opacity: 1;
}

.tooltipDocuments-container {
  position: relative;
}
.swal2-popup.responsive-popup {
  font-size: 16px; /* Asegura que el texto no se vea demasiado grande en pantallas pequeñas */
}

@media screen and (max-width: 768px) {
  .modal-content {
    width: 95%;
    
  }

  .courses-table th,
  .courses-table td {
    padding: 8px;
    font-size: 14px;
  }

  .header input[type="text"] {
    font-size: 14px;
  }

  .modal-footer {
    padding: 10px;
  }

  .modal-buttons button {
    font-size: 14px;
  }
  .swal2-popup .responsive-popup{
    width: 70% !important; /* Hacemos el modal más pequeño en pantallas pequeñas */
    padding: 15px;
  }

  .swal2-title {
    font-size: 18px; /* Ajusta el tamaño del título */
  }

  .swal2-text {
    font-size: 14px; /* Ajusta el tamaño del texto */
  }

  .swal2-confirm, .swal2-cancel {
    padding: 10px 20px;
  }
}
</style>
