<template>
  <div class="profile-modal-overlay" v-if="isVisible" @click="handleCloseModal">
    <div class="profile-modal-content" @click.stop>
      <div v-if="empleadoData">
        <ProfileHeader :empleadoData="empleadoData" />
        <ProfileTabs 
          :currentTab="currentTab" 
          :status="statusDocuments" 
          @update:tab="currentTab = $event" 
          @update:status="statusDocuments = $event" 
        />

        <div class="tab-content">
          <div v-if="currentTab === 'general'">
            <GeneralInfo 
              :empleadoData="empleadoData" 
              :updateEmpleado="updateEmpleado" 
              @close="handleCloseModal" 
              @empleadoActualizado="$emit('empleadoActualizado', $event)" 
            />
          </div>
          <div v-if="currentTab === 'medical'">
            <MedicalInfo 
              :empleadoData="empleadoData" 
              @close="handleCloseModal" 
              @empleadoActualizado="$emit('empleadoActualizado', $event)" 
            />
          </div>
          <div v-if="currentTab === 'documents'">
            <Documents 
              :empleadoData="empleadoData" 
              :openAddDocumentModal="openAddDocumentModal" 
              @close="handleCloseModal" 
            />
          </div>
          <div v-if="currentTab === 'bgv'">
            <Bgv 
              :empleadoData="empleadoData" 
              :openAddExamtModal="openAddExamModal" 
              :idCliente="idCliente" 
              @close="handleCloseModal" 
            />
          </div>
        </div>

        <AddDocumentModal 
          v-if="showAddDocumentModal" 
          :isVisible="showAddDocumentModal" 
          :employeeId="empleadoData.id" 
          :id_portal="empleadoData.id_portal" 
          @close="closeAddDocumentModal"  
        />
        <AddExamModal 
          v-if="showAddExamModal" 
          :isVisible="showAddExamModal" 
          :employeeId="empleadoData.id" 
          :id_portal="empleadoData.id_portal" 
          @close="closeAddExamModal"  
        />
      </div>
    </div>
  </div>
</template>

<script>
import AddDocumentModal from './AddDocumentModal.vue';
import AddExamModal from './AddExamModal.vue';
import ProfileHeader from './PerfilHeader.vue';
import ProfileTabs from './PerfilTabs.vue';
import GeneralInfo from './GeneralInfo.vue';
import MedicalInfo from './MedicalInfo.vue';
import Documents from './PerfilDocuments.vue';
import Bgv from './PerfilEse.vue';

export default {
  name: 'ProfileModal',
  components: {
    AddExamModal,
    AddDocumentModal,
    ProfileHeader,
    ProfileTabs,
    GeneralInfo,
    MedicalInfo,
    Documents,
    Bgv
  },
  props: {
    empleado: Object,
    isVisible: Boolean,
    idCliente: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      currentTab: 'general',
      showAddDocumentModal: false,
      showAddExamModal: false,
      documentType: '',
      empleadoData: null,
      error: null,
      statusDocuments: this.empleado ? this.empleado.statusDocuments : null, // Cambiado aquí
    };
  },
  methods: {
    handleCloseModal() {
      this.$emit('close');
      this.currentTab = 'general';
      this.empleadoData = null; // Limpia los datos del empleado
    },
    openAddDocumentModal(type = 'general') {
      this.documentType = type;
      this.showAddDocumentModal = true;
    },
    closeAddDocumentModal() {
      this.showAddDocumentModal = false;
    },
    openAddExamModal(type = 'general') {
      this.documentType = type;
      this.showExamModal = true;
    },
    closeAddExamModal() {
      this.showAddExamModal = false;
    },
  
    updateEmpleado(updatedData) {
      this.empleadoData = { ...this.empleadoData, ...updatedData };
      this.$emit('empleadoActualizado', updatedData); // Emitir el evento de actualización
    },
  },
  watch: {
    empleado(newValue) {
      this.statusDocuments = newValue ? newValue.statusDocuments : null; // Cambiado aquí
      this.$emit('update:status', this.statusDocuments); // Emitir el nuevo status
    },
    isVisible(newValue) {
      if (newValue) {
        this.empleadoData = { ...this.empleado }; // Crea una copia de los datos
        this.currentTab = 'general';
      } else {
        this.currentTab = 'general';
        this.empleadoData = null; // Limpia los datos
      }
    }
  }
};
</script>




<style scoped>
.profile-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
