  font-size: 16px;
  cursor: pointer;
  color: #007bff;
  background: #f0f0f0;
  padding: 5px 10px;
  border-radius: 4px;
  text-align: center;
}

.profile-info {
  flex-grow: 1;
  text-align: center;
}

.profile-info h2 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.profile-extra-info {
  text-align: right;
  max-width: 250px;
}

.profile-extra-info p {
  margin: 5px 0;
  font-size: 16px;
  color: #555;
}

.profile-extra-info p strong {
  color: #007bff;
}

@media (max-width: 600px) {
  .profile-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .profile-picture {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.profile-tabs {
  display: flex;
  border-bottom: 1px solid #ddd;
  margin-bottom: 15px;
}

.profile-tabs button {
  background: none;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 18px;
