  cursor: pointer;
  margin-left: 5px;
}

.close-button {
  background: #dc3545;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 10px;
}



.close-button:hover {
  background-color: #0056b3;
}

.employee-form {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.form-section {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
}

.form-section legend {
  font-weight: bold;
  margin-bottom: 10px;
}

.form-grid {
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(1, 1fr);
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.update-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 10px;
  /* Espacio entre botones */
}

.update-button:hover {
  background-color: #0056b3;
}

.close-button {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
}

.close-button:hover {
  background-color: #c82333;
}

.button-container {
  display: flex;
  gap: 10px;
  /* Espacio entre botones */
}

/* Media Queries */
@media (min-width: 600px) {
  .form-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 900px) {
  .form-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.alert-symbol {
  display: inline-block;
  width: 10px;
  /* Tamaño del círculo */
  height: 10px;
  /* Tamaño del círculo */
  border-radius: 50%;
  /* Hace que el elemento sea un círculo */
  margin-left: 5px;
  /* Espacio a la izquierda del círculo */
}

.amarillo {
  background-color: yellow;
  /* Color amarillo */
}

.rojo {
  background-color: red;
  /* Color rojo */
}
</style>