<template>
  <div>
    <form @submit.prevent="updateMedicalInfo" class="employee-form">
      <fieldset class="form-section">
        <legend>Medical Information</legend>
        <div class="form-grid">
          <div class="form-group">
            <label for="peso">Weight (kg):</label>
            <input type="number" id="peso" v-model="localEmpleadoData.peso" placeholder="Weight" />
          </div>
          <div class="form-group">
            <label for="edad">Age:</label>
            <input type="number" id="edad" v-model="localEmpleadoData.edad" placeholder="Age" />
          </div>
          <div class="form-group">
            <label for="alergias_medicamentos">Medication Allergies:</label>
            <input type="text" id="alergias_medicamentos" v-model="localEmpleadoData.alergias_medicamentos"
              placeholder="Allergies to medications" />
          </div>
          <div class="form-group">
            <label for="alergias_alimentos">Food Allergies:</label>
            <input type="text" id="alergias_alimentos" v-model="localEmpleadoData.alergias_alimentos"
              placeholder="Allergies to food" />
          </div>
          <div class="form-group">
            <label for="enfermedades_cronicas">Chronic Diseases:</label>
            <input type="text" id="enfermedades_cronicas" v-model="localEmpleadoData.enfermedades_cronicas"
              placeholder="Chronic diseases" />
          </div>
          <div class="form-group">
            <label for="cirugias">Surgeries:</label>
            <input type="text" id="cirugias" v-model="localEmpleadoData.cirugias" placeholder="Previous surgeries" />
          </div>
          <div class="form-group">
            <label for="tipo_sangre">Blood Type:</label>
            <input type="text" id="tipo_sangre" v-model="localEmpleadoData.tipo_sangre" placeholder="Blood type" />
          </div>
          <div class="form-group">
            <label for="contacto_emergencia">Emergency Contact:</label>
            <input type="text" id="contacto_emergencia" v-model="localEmpleadoData.contacto_emergencia"
              placeholder="Emergency contact" />
          </div>
          <div class="form-group">
            <label for="medicamentos_frecuentes">Frequent Medications:</label>
            <input type="text" id="medicamentos_frecuentes" v-model="localEmpleadoData.medicamentos_frecuentes"
              placeholder="Frequent medications" />
          </div>
          <div class="form-group">
            <label for="lesiones">Chronic Injuries / Fractures:</label>
            <input type="text" id="lesiones" v-model="localEmpleadoData.lesiones"
              placeholder="Chronic injuries or fractures" />
          </div>
          <div class="form-group two-column">
            <label for="otros_padecimientos">Other Conditions:</label>
            <textarea id="otros_padecimientos" v-model="localEmpleadoData.otros_padecimientos"
              placeholder="Describe any other conditions..." rows="4"></textarea>
          </div>
          <div class="form-group two-column">
            <label for="otros_padecimientos2">Additional Conditions:</label>
            <textarea id="otros_padecimientos2" v-model="localEmpleadoData.otros_padecimientos2"
              placeholder="Describe any additional conditions..." rows="4"></textarea>
          </div>
        </div>
      </fieldset>

      <div class="button-container">
        <button type="submit" class="update-button">Update</button>
        <button class="close-button " type="button" @click="handleCloseModal">Close</button>

      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';
import moment from 'moment-timezone';

export default {
  name: 'MedicalInfoForm',
  props: {
    empleadoData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      localEmpleadoData: { ...this.empleadoData } // Crea una copia local
    };
  },
  created() {
    this.fetchMedicalInfo();
  },
  methods: {
    fetchMedicalInfo() {
      const employeeId = this.localEmpleadoData.id; // Obtiene el ID del empleado
     // console.log("🚀 ~ fetchMedicalInfo ~ employeeId:", employeeId)
      const csrfMetaTag = document.querySelector('meta[name="csrf-token"]');
      const csrfToken = csrfMetaTag ? csrfMetaTag.getAttribute('content') : null;

      if (!csrfToken) {
        console.error('CSRF token no encontrado');
        return; // Evita enviar la solicitud si el token no está presente
      }

      axios.get(`${this.$apiUrl}/medical-info/${employeeId}`, {
        headers: {
          'X-CSRF-TOKEN': csrfToken
        }
      })
        .then(response => {
          this.localEmpleadoData = response.data; // Actualiza la copia local con la respuesta
        })
        .catch(error => {
          console.error('Error fetching medical info:', error.response ? error.response.data : error.message);
        });
    },
    handleCloseModal() {
      this.$emit('close'); // Emite el evento para cerrar el modal
    },
    async updateMedicalInfo() {
      const filteredData = Object.fromEntries(
        Object.entries(this.localEmpleadoData).filter(([, value]) => value !== '' && value !== null)
      );

      // Establecer la fecha de edición
      filteredData.edicion = moment.tz('America/Mexico_City').format('YYYY-MM-DD HH:mm:ss');

      const employeeId = this.localEmpleadoData.id_empleado; // Obtiene el ID del empleado
      const apiUrl = `${this.$apiUrl}/medical-info/${employeeId}`;

      try {
        console.log('Datos a enviar a la API:', JSON.stringify(filteredData, null, 2));

        

        if (!this.$csrfToken) {
          console.error('CSRF token no encontrado');
          return; // Evita enviar la solicitud si el token no está presente
        }

        const response = await axios.put(apiUrl, filteredData, {
          headers: {
            'X-CSRF-TOKEN': this.$csrfToken
          }
        });

        console.log('Respuesta de la API:', response);

        // Mostrar SweetAlert de éxito
        Swal.fire({
          icon: 'success',
          title: 'Actualización exitosa',
          text: 'La información médica se ha actualizado correctamente.',
          confirmButtonText: 'Aceptar'
        }).then(() => {
          this.$emit('updateEmpleado', filteredData);
          this.handleCloseModal(); // Cerrar modal
        });
      } catch (error) {
        let errorMessage = 'Ocurrió un error al actualizar la información médica.';

        if (error.response) {
          errorMessage = error.response.data.error || 'Error desconocido';
          console.error('Error al actualizar la información médica:', error.response.data);
        } else if (error.request) {
          console.error('No se recibió respuesta del servidor:', error.request);
          errorMessage = 'No se recibió respuesta del servidor.';
        } else {
          console.error('Error en la configuración de la solicitud:', error.message);
          errorMessage = error.message;
        }

        // Mostrar SweetAlert de error
        Swal.fire({
          icon: 'error',
          title: 'Error',
          text: errorMessage,
          confirmButtonText: 'Aceptar'
        });
      }
    }

  },
  watch: {
    empleadoData: {
      handler(newValue) {
        this.localEmpleadoData = { ...newValue }; // Actualiza la copia local si cambia la prop
      },
      deep: true
    }
  }
};
</script>

<style scoped>
.employee-form {
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.form-section {
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
}

.form-section legend {
  font-weight: bold;
  margin-bottom: 10px;
}

.form-grid {
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(1, 1fr);
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group label {
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.update-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-right: 10px;
  /* Space between buttons */
}

.update-button:hover {
  background-color: #0056b3;
}

.close-button {
  background-color: #dc3545;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  cursor: pointer;
}

.close-button:hover {
  background-color: #c82333;
}

.button-container {
  display: flex;
  justify-content: flex-end;
  /* Align buttons to the right */
  gap: 10px;
}



.two-column textarea {
  grid-column: span 2;
  /* Hacer que el textarea ocupe las dos columnas */
}

/* Media Queries */
@media (min-width: 600px) {
  .form-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 900px) {
  .form-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
</style>
