<template>
  <div id="app">
    <Header 
      :modules="modules" 
      :selectedModule="selectedModule" 
      :idPortal="idPortal" 
      @select-module="handleSelectModule"
      :idCliente="idCliente"
      :isMobile="isMobile"  
    />
    <ContentSection 
      :currentComponent="currentComponent" 
      :idPortal="idPortal" 
      :idUsuario="idUsuario" 
      :idCliente="idCliente" 
    />
  </div>
</template>

<script>
import Header from './components/EmpleadosHeader.vue';
import ContentSection from './components/ContentSection.vue';
import Module1 from './components/EmpleadosExpediente.vue';
import Module2 from './components/EmpleadosCursos.vue';
import Module3 from './components/EmpleadosEvaluacion.vue';

export default {
  components: {
    Header,
    ContentSection
  },
  props: {
    idPortal: {
      type: String,
      default: ''
    },
    idUsuario: {
      type: String,
      default: ''
    },
    idCliente: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      modules: [
        { id: 'module1', title: 'Employee File', component: Module1, statusKey: 'documentos', icon: 'fas fa-file-alt' },
        { id: 'module2', title: 'Courses and Training', component: Module2, statusKey: 'cursos', icon: 'fas fa-graduation-cap' },
        { id: 'module3', title: 'Evaluations', component: Module3, statusKey: 'evaluaciones', icon: 'fas fa-chart-line' }
      ],
      selectedModule: 'module1',
      isMobile: false // Agregar el estado isMobile aquí
    };
  },
  computed: {
    currentComponent() {
      const module = this.modules.find(m => m.id === this.selectedModule);
      return module ? module.component : null;
    }
  },
  methods: {
    handleSelectModule(id) {
      this.selectedModule = id;
    },
    checkIfMobile() {
      this.isMobile = window.innerWidth <= 768; // Actualizar el estado de isMobile basado en el tamaño de la pantalla
    }
  },
  mounted() {
    this.checkIfMobile(); // Verificar si es móvil al cargar
    window.addEventListener('resize', this.checkIfMobile); // Escuchar los cambios de tamaño de la ventana
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkIfMobile); // Limpiar el evento al destruir el componente
  }
};
</script>

<style scoped>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}

/* Estilo para el header */
header {
  background-color: #f4f4f4;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

/* Estilo para la sección de contenido */
.ContentSection {
  flex: 1;
  padding: 20px;
  overflow-x: auto;
}

/* Puntos de ruptura */
@media (max-width: 768px) {
  #app {
    font-size: 14px;
  }

  header {
    padding: 5px;
    font-size: 14px;
  }

  .ContentSection {
    padding: 10px;
  }
}

@media (max-width: 480px) {
  #app {
    font-size: 12px;
  }

  header {
    font-size: 12px;
  }

  .ContentSection {
    padding: 5px;
  }
}
</style>
